div{
  font-family: "CustomFont", sans-serif; /* NotoSansKR 폰트를 적용 */
}

.AppHeader{
  display: flex;
  height: 6.5vh;
  justify-content: flex-start;
  background-color: #ffebee;
  opacity: 0.85;
}

.AppHeader-left{
  text-align: left;
  float: left;
  width: 80vw;
  background-image: url(jjinicon.png);
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  margin-left: 35vw;
}

.AppHeader-right{
  text-align: left;
  float: right;
  width: 99%;
  background-image: url(Hamburger_icon.png);
  background-size: 30px;
  background-repeat: no-repeat;
  background-position: right;
  margin-right: 2vw;
}

.AppBody{
  height: 93.5vh;
}

.body-1{
  text-align: center;
  margin-top: 4vh;
}

.body-1-1{
  font-family: "NotoSansKR", sans-serif;
  font-size: 1.2rem;
  font-weight: bold;
}

.body-1-2{
  font-size: 2.3rem;
  color: #f48fb1;
  margin-top: 1.5vh;
}

.content-body-2{
  display: flex;
  margin-top: 50px;
  justify-content: flex-start;
}

.content-body-2-left{
  text-align: left;
  float: left;
  width: 3vw;
  background-image: url(dot.png);
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;  
}

.content-body-2-right{
  text-align: left;
  float: right;
  width: 99%;
  font-size: 20px;
}

